* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    background: var(--white);
    font-family: arial, helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

a {
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

img {
    max-width: 100%;
}

.header {
    padding: 20px;
    color: #fff;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}

.header .header-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header .header-content .header-logo-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.header .header-content .header-logo-container img {
    margin-right: 15px;
    width: 50px;
    height: 40px;
}

.header .header-content .header-logo-container h2 {
    font-size: 20px;
    line-height: 24px;
}

@media screen and (max-width: 767px) {
    .header .header-content .header-text-container p {
        text-align: right;
    }
}

.meme-form-container {
    padding-bottom: 75px;
    margin: 36px 0;
}

.meme-form-container .form {
    max-width: 650px;
    width: 100%;
    margin: 0 auto;
}

.meme-form-container .form .form-input-fields {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.meme-form-container .form .form-input-fields input {
    max-width: 315px;
    width: 100%;
    height: 35px;
    padding: 10px;
    border: 1px solid #D5D4D8;
    border-radius: 5px;
}

.form-submit-btn-container {
    margin-bottom: 36px;
}

.form-submit-btn-container .form-button {
    display: inline-block;
    max-width: 100%;
    width: 100%;
    height: 40px;
    padding: 10px 0;
    font-size: 16px;
    font-weight: 700;
    color: #fff;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    line-height: 22px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-submit-btn-container .form-download-meme {
    text-align: center;
    margin-top: 20px;
}

.meme {
    position: relative;
}

.meme-form-container .meme-image {
    display: block;
    width: 100%;
}

.meme-text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.top {
    top: 20px;
}

.bottom {
    bottom: 10px;
}

.footer {
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    color: #fff;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}

.footer a {
    color: #fff;
    text-decoration: underline;
}

@media screen and (max-width: 768px) {
    .form {
        padding: 0 10px;
    }
    
    .meme-form-container .form .form-input-fields {
        flex-direction: column;
        justify-content: center;
        gap: 10px 0;
    }

    .meme-form-container .form .form-input-fields input {
        max-width: 100%;
        width: 100%;
    }

    .form-submit-btn-container .form-download-meme {
        margin-top: 15px;
    }

    .top {
        top: 2px;
    }

    .bottom {
        bottom: 2px;
    }
    
    .meme-text {
        font-size: 1.2em;
    }
}